import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../../services/validate.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
	name:String;
	phone:String;
	remark:String;

  constructor(
  	private validateService:ValidateService,
  	private flashMessage: FlashMessagesService,
  	private authService:AuthService,
  	private router:Router

  	) { }

  ngOnInit() {
  }

   onFormSubmit(){
   	const form = {
   		name:this.name,
   		phone:this.phone,
   		remark:this.remark
   	}
   	 // required field
    if(!this.validateService.validateForm(form)){
      console.log("all field are required");
      this.flashMessage.show('all field are required', { cssClass: 'alert-danger', timeout: 3000 });
      return false;
    }

   	this.authService.formSubmit(form).subscribe(data=>{
      if (data.success) {
        this.flashMessage.show('Thank you for form submission', { cssClass: 'alert-success', timeout: 3000 });
        this.name = '';
        this.phone = '';
        this.remark = '';
        this.router.navigate(['dashboard'])
      }else{
        this.flashMessage.show('Something went wrong', { cssClass: 'alert-danger', timeout: 3000 })
        this.router.navigate(['dashboard'])
      }
    });
   }

}



