import { Component, OnInit } from '@angular/core';
import {ValidateService} from '../../services/validate.service';
import {AuthService} from '../../services/auth.service';
import {Router} from '@angular/router';

import { FlashMessagesService } from 'angular2-flash-messages';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
	name: String;
	username: String;
	email: String;
	password: String;

  constructor(
    private validateService:ValidateService,
    private flashMessage: FlashMessagesService,
    private authService:AuthService,
    private router:Router
    ) { }

  ngOnInit() {
  }

  onRegisterSubmit(){
  	const user = {
  		name: this.name,
  		username: this.username,
  		email: this.email,
  		password: this.password
  	}

    // required field
    if(!this.validateService.validateRegister(user)){
      console.log("all field are required");
      this.flashMessage.show('all field are required', { cssClass: 'alert-danger', timeout: 3000 });
      return false;
    }

    // validte email
    if(!this.validateService.validateEmail(user.email)){
      console.log("email is not valid");
      this.flashMessage.show('email is not valid', { cssClass: 'alert-danger', timeout: 3000 })
      return false;
    }
  	
    // register User
    this.authService.registerUser(user).subscribe(data=>{
      if (data.success) {
        this.flashMessage.show('You are register n can login now', { cssClass: 'alert-success', timeout: 3000 })
        this.router.navigate(['/login'])
      }else{
        this.flashMessage.show('Something went wrong', { cssClass: 'alert-danger', timeout: 3000 })
        this.router.navigate(['/register'])
      }
    });
  }

}



