import { Injectable } from '@angular/core';
import {Http,Headers} from '@angular/http';
import 'rxjs/add/operator/map'
import { tokenNotExpired } from 'angular2-jwt';
@Injectable()
export class AuthService {
	authToken:any;
	user:any

  constructor(private http:Http) { }
  registerUser(user){
  	let headers = new Headers();
  	headers.append('content-type','application/json');
  	return this.http.post('http://localhost:5000/users/register',user,{headers:headers})
  	.map(res=>res.json());
  }

  authenticateUser(user){
  	let headers = new Headers();
  	headers.append('content-type','application/json');
  	return this.http.post('http://localhost:5000/users/authenticate',user,{headers:headers})
  	.map(res=>res.json());
  }

  storeUserData(token,user){
  	localStorage.setItem('id_token',token);
  	localStorage.setItem('user',JSON.stringify(user));
  	this.authToken = token;
  	this.user = user;
  }

  getProfile(){
  	let headers = new Headers();
  	this.loadToken();
  	headers.append('Authorization',this.authToken);
  	headers.append('content-type','application/json');
  	return this.http.get('http://localhost:5000/users/profile',{headers:headers})
  	.map(res=>res.json());
  }


  loadToken(){
  	const token = localStorage.getItem('id_token');
  	this.authToken = token;
  }

  loggedIn() {
    return tokenNotExpired('id_token');
  }

  logout(){
  	this.authToken = null;
  	this.user = null;
  	localStorage.clear();
  }

  // for form submission

  formSubmit(form){
    let headers = new Headers();
    headers.append('content-type','application/json');
    return this.http.post('http://localhost:5000/users/form',form,{headers:headers})
    .map(res=>res.json());
  }

}



